import * as React from 'react';
import { baseApiUrl, getErrorMessage, getRequestOptions, getSuccessMessage, getUserId, serializeData } from '../utilities/emsUtilities';
import { Button, Paper, Grid, TextField, Modal, FormControl, InputLabel, Select, MenuItem, TextareaAutosize } from '@material-ui/core';
import { EMSSubmitTab } from './EMSSubmitTab'
import { EMSDetail } from './EMSDetail';

export interface ComplaintProps {
    ref: any;
    getEMSComplaint: Function;
    getEMSCategory: Function;
    EMSComplaint: any
    PageName: string;
    handleDetailShow: Function;
}

export class EMSSubmitUpdatePopup extends React.Component<ComplaintProps, {}>{

    state = {
        EMSComplaint: {
            ComplaintId: 0,
            ComplaintStatus: '',
            SubmitterName: '',
            SubmitterPhoneNo: '',
            SubmitterShit: '',
            RequestDate: '',
            Location: '',
            CategoryId: 0,
            SubCategoryId: 0,
            SubCategoryOthers: '',
            Unit: '',
            VehicleNumber: '',
            AssetNumber: '',
            ComplaintPriority: 0,
            SerialNumber: '',
            IncidentNumber: '',
            ComplaintDetail: '',
            CommentsByAdmin: '',
            CreatedDate: '',
            CreatedBy: '',
            UpdatedDate: '',
            UpdatedBy: ''
        }, EMSCategoryList: [{
            CategoryId: 0,
            ParentId: 0,
            CategoryType: '',
            CategoryCode: '',
            CategoryDesc: '',
            SortOrder: 0,
            CreatedDate: '',
            CreatedBy: '',
            UpdatedDate: '',
            UpdatedBy: ''
        }], EMSSubCategoryList: [{
            CategoryId: 0,
            ParentId: 0,
            CategoryType: '',
            CategoryCode: '',
            CategoryDesc: '',
            SortOrder: 0,
            CreatedDate: '',
            CreatedBy: '',
            UpdatedDate: '',
            UpdatedBy: ''
        }], Email: {
            From: 'DoNotReply@fairfaxcounty.gov',
            To: [''],
            CC: [''],
            BCC: ['ykim01@fairfaxcounty.gov'],
            Subject: '',
            Body: ''
        }, UnitList: [{
            Id: 0,
            Unit: '',
            WorkGroupId: '',
            WorkGroupAbbrv: ''
        }], WorkGroupList: [{
            Id: 0,
            WorkGroupAbbrv: '',
            WorkGroupName: '',
            battalion: ''
        }],
        show: false, canSave: false, PageName: ''
    }

    eMSSubmitTab = React.createRef<EMSSubmitTab>();
    eMSDetail = React.createRef<EMSDetail>();
    userId = '';

    constructor(props: any) {
        super(props);
        this.userId = getUserId();
    }

    // get workgroup
    getWorkGroup = () => {
        const url = baseApiUrl() + 'Lookup/Locations';

        fetch(url, getRequestOptions).then(res => {
            if (res.status === 200)
                return res.json();
            return res.status;
        }).then(responseData => {
            this.setState({ WorkGroupList: responseData });
            console.log(this.state.WorkGroupList);
        }).catch((errors) => {
            //console.log(errors);
        });
    }

    // get unit
    getUnit = () => {
        const url = baseApiUrl() + 'Lookup/Units';

        fetch(url, getRequestOptions).then(res => {
            if (res.status === 200)
                return res.json();
            return res.status;
        }).then(responseData => {
            this.setState({ UnitList: responseData });
            console.log(this.state.UnitList);
        }).catch((errors) => {
            //console.log(errors);
        });
    }


    // get ems category
    getEMSCategory = () => {
        const url = baseApiUrl() + 'EMSCategoryByType?type=Category';

        fetch(url, getRequestOptions).then(res => {
            if (res.status === 200)
                return res.json();
            return res.status;
        }).then(responseData => {
            this.setState({ EMSCategoryList: responseData });
            console.log(this.state.EMSCategoryList);
        }).catch((errors) => {
            //console.log(errors);
        });
    }


    // get ems sub category
    getEMSSubCategory = (value: number) => {
        const url = baseApiUrl() + 'EMSSubCategoryById?Id=' + value;

        fetch(url, getRequestOptions).then(res => {
            if (res.status === 200)
                return res.json();
            return res.status;
        }).then(responseData => {
            this.setState({ EMSSubCategoryList: responseData });
            console.log(this.state.EMSSubCategoryList);
        }).catch((errors) => {
            //console.log(errors);
        });
    }

    //set state
    handleChange = (event: any) => {
        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;
        const currentSQState = { ...this.state.EMSComplaint, [name]: value };
        this.setState({ EMSComplaint: currentSQState });
        if (this.state.EMSComplaint.ComplaintDetail !== null && this.state.EMSComplaint.ComplaintDetail !== 'undefined' && this.state.EMSComplaint.IncidentNumber !== null && this.state.EMSComplaint.IncidentNumber !== 'undefined') {
            this.setState({ canSave: true });
        } else
            this.setState({ canSave: false });
        if (name === 'CategoryId' && (value !== null && value > 0)) {
            this.getEMSSubCategory(value);
        }
    }


    //Insert Complaint
    handleSubmit = () => {
        if (this.state.EMSComplaint.Unit && this.state.EMSComplaint.Unit !== null) {
            if (this.state.EMSComplaint.SerialNumber.length > 0) {
                if (this.state.EMSComplaint.AssetNumber.length > 0) {
                    let ComplaintUrl = baseApiUrl() + 'Complaint/Update';

                    const today = new Date().toISOString();

                    /*this.state.EMSComplaint.ComplaintStatus = 'Submitted';*/
                    this.state.EMSComplaint.RequestDate = today;
                    //this.state.EMSComplaint.CreatedBy = this.userId;
                    //this.state.EMSComplaint.CreatedDate = today;
                    this.state.EMSComplaint.UpdatedBy = this.userId;
                    this.state.EMSComplaint.UpdatedDate = today;

                    let requestSQOptions = {
                        method: 'POST',
                        headers: {
                            'Content-Type': 'application/x-www-form-urlencoded'
                        },
                        body: serializeData(this.state.EMSComplaint)
                    }

                    fetch(ComplaintUrl, requestSQOptions).then(res => {
                        if (res.status === 200)
                            return res.json();
                        return res.status;
                    }).then(responseData => {
                        if (responseData === 400)
                            alert(getErrorMessage('This is a Bad Request.'));
                        else if (responseData === 401)
                            alert(getErrorMessage('You are not authorized.'));
                        else if (responseData === 403)
                            alert(getErrorMessage('You don\'t have the access rights.'));
                        else if (responseData === 404)
                            alert(getErrorMessage('There is no data.'));
                        else if (responseData === 409)
                            alert(getErrorMessage('There is a conflict.'));
                        else if (responseData === 500)
                            alert(getErrorMessage('There is an internal server error.'));
                        else {
                            this.setState({ EMSComplaint: responseData });
                            alert(getSuccessMessage('a complaint'));
                        }
                    }).catch((errors) => {
                        // console.log(errors);
                        alert(getErrorMessage('There is an error during inserting a complaint'));
                    })
                } else
                    ('Please input the Unit');
            } else
                alert('Please input the Security Number');
        } else
            alert('Please input the County Asset Number');
    }


    //clear state
    handleClear = () => {
        this.setState({
            EMSComplaint: {
                ComplaintId: 0,
                ComplaintStatus: '',
                SubmitterName: '',
                SubmitterPhoneNo: '',
                SubmitterShit: '',
                RequestDate: '',
                Loacation: '',
                CategoryId: 0,
                SubCategoryId: 0,
                SubCategoryOthers: '',
                Unit: '',
                VehicleNumber: '',
                AssetNumber: '',
                ComlaintPriority: 0,
                SerialNumber: '',
                IncidentNumber: '',
                ComplaintDetail: '',
                CommentByAdmin: '',
                CreatedDate: '',
                CreatedBy: '',
                UpdatedDate: '',
                UpdatedBy: ''
            }
        });
    }

    handleDelete = (id: number) => {
        console.log(id);
        this.setComplaint();
        const delDto = {
            Id: id,
            ForeignKey: 0
        };

        const deleteRequestOptions = {
            method: 'POST',
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            },
            body: serializeData(delDto)
        };

        fetch(baseApiUrl() + 'Complaint/Delete', deleteRequestOptions).then(res => {
            if (res.status === 200)
                return res.json();
            return res.status;
        }).then(responseData => {
            if (responseData === 400)
                alert(getErrorMessage('This is a Bad Request.'));
            else if (responseData === 401)
                alert(getErrorMessage('You are not authorized.'));
            else if (responseData === 403)
                alert(getErrorMessage('You don\'t have the access rights.'));
            else if (responseData === 404)
                alert(getErrorMessage('There is no data.'));
            else if (responseData === 409)
                alert(getErrorMessage('There is a conflict.'));
            else if (responseData === 500)
                alert(getErrorMessage('There is an internal server error.'));
            else {
                this.props.getEMSComplaint.call(this);
                alert("It has been removed successfully.");
            }
        }).catch((errors) => {
            /*console.log(errors);*/
            alert(getErrorMessage('There is an error during deleting a complaint'));
        });

        this.handleClear();
    }



    handleShow = () => {
        this.setState({ show: true });
        this.setComplaint();
    }

    handleDetailShow = () => {
        this.props.handleDetailShow.call(this);
    }

    handleClose = () => {
        this.setState({ show: false });
        this.props.getEMSComplaint();
    }

    setComplaint = () => {
        this.state.EMSComplaint.ComplaintId = this.props.EMSComplaint.ComplaintId;
        this.state.EMSComplaint.ComplaintStatus = this.props.EMSComplaint.ComplaintStatus;
        this.state.EMSComplaint.SubmitterName = this.props.EMSComplaint.SubmitterName;
        this.state.EMSComplaint.SubmitterPhoneNo = this.props.EMSComplaint.SubmitterPhoneNo;
        this.state.EMSComplaint.SubmitterShit = this.props.EMSComplaint.SubmitterShit;
        this.state.EMSComplaint.RequestDate = this.props.EMSComplaint.RequestDate;
        this.state.EMSComplaint.Location = this.props.EMSComplaint.Location;
        this.state.EMSComplaint.CategoryId = this.props.EMSComplaint.CategoryId;
        this.state.EMSComplaint.SubCategoryId = this.props.EMSComplaint.SubCategoryId;
        this.state.EMSComplaint.SubCategoryOthers = this.props.EMSComplaint.SubCategoryOthers;
        this.state.EMSComplaint.Unit = this.props.EMSComplaint.Unit;
        this.state.EMSComplaint.VehicleNumber = this.props.EMSComplaint.VehicleNumber;
        this.state.EMSComplaint.AssetNumber = this.props.EMSComplaint.AssetNumber;
        this.state.EMSComplaint.ComplaintPriority = this.props.EMSComplaint.ComplaintPriority;
        this.state.EMSComplaint.SerialNumber = this.props.EMSComplaint.SerialNumber;
        this.state.EMSComplaint.IncidentNumber = this.props.EMSComplaint.IncidentNumber;
        this.state.EMSComplaint.ComplaintDetail = this.props.EMSComplaint.ComplaintDetail;
        this.state.EMSComplaint.CommentsByAdmin = this.props.EMSComplaint.CommentsByAdmin;
        this.state.EMSComplaint.CreatedDate = this.props.EMSComplaint.CreatedDate;
        this.state.EMSComplaint.CreatedBy = this.props.EMSComplaint.CreatedBy;
        this.state.EMSComplaint.UpdatedDate = this.props.EMSComplaint.UpdatedDate;
        this.state.EMSComplaint.UpdatedBy = this.props.EMSComplaint.UpdatedBy;
    }


    render() {
        return (
            <Grid item xs={12} style={{ textAlign: "left" }}>
                <Grid container className="textRight alignRight" style={{ paddingBottom: "5px" }}>
                    <Button variant="contained" onClick={() => { this.handleDetailShow() }} data-toggle="tooltip" title="Detail" className="marginLeft5" style={{ backgroundColor: 'orange' }}><img src={window.location.origin + '/images/MORE_white.png'} className="btnImage" /></Button>
                    <Button variant="contained" color="primary" onClick={() => { this.handleShow(); }} data-toggle="tooltip" title="Update Log" className="marginLeft5"><img src={window.location.origin + '/images/edit_white.png'} className="btnImage" /></Button>
                    <Button variant='contained' color="secondary" onClick={() => { window.confirm('Are you sure you wish to delete this Log?') && this.handleDelete.call(this, this.props.EMSComplaint.ComplaintId); }} data-toggle="tooltip" title="Delete Log" className="marginLeft5"><img src={window.location.origin + '/images/delete_white.png'} className="btnImage" /></Button>
                </Grid>
                <Modal className="insertModal" open={this.state.show} onClose={() => { this.handleClear.call(this); this.handleClose(); }} disableBackdropClick={true}>
                    <Paper className="ModalDiv">
                        <Grid container spacing={2}>
                            <Grid item xs={4}>
                                <FormControl variant="outlined" className="selectForm" style={{ width: '100%' }}>
                                    <InputLabel id="demo-simple-select-outlined-label" style={{ fontSize: 13 }}>*Category</InputLabel>
                                    <Select
                                        labelId="demo-simple-select-outlined-label"
                                        id="demo-simple-select-outlined"
                                        name="CategoryId"
                                        label="Category"
                                        value={this.state.EMSComplaint.CategoryId}
                                        onChange={this.handleChange}>
                                        <MenuItem value=""><em>Select One</em></MenuItem>
                                        {this.state.EMSCategoryList.map((item) => (
                                            <MenuItem value={item.CategoryId}><em>{item.CategoryDesc}</em></MenuItem>
                                        ))}
                                    </Select>
                                </FormControl>
                            </Grid>
                            <Grid item xs={4}>
                                <FormControl variant="outlined" className="selectForm" style={{ width: '100%' }}>
                                    <InputLabel id="demo-simple-select-outlined-label" style={{ fontSize: 13 }}>*Subcategory</InputLabel>
                                    <Select
                                        labelId="demo-simple-select-outlined-label"
                                        id="demo-simple-select-outlined"
                                        name="SubCategoryId"
                                        label="SubCategory"
                                        value={this.state.EMSComplaint.SubCategoryId}
                                        onChange={this.handleChange}>
                                        <MenuItem value=""><em>Select One</em></MenuItem>
                                        {this.state.EMSSubCategoryList.map((item) => (
                                            <MenuItem value={item.CategoryId}><em>{item.CategoryDesc}</em></MenuItem>
                                        ))}
                                    </Select>
                                </FormControl>
                            </Grid>
                            {this.props.PageName && this.props.PageName === 'Admin' ?
                                <Grid item xs={4} sm={4}>
                                    <FormControl variant="outlined" className="selectForm" style={{ width: '100%' }}>
                                        <InputLabel id="demo-simple-select-outlined-label">Report Status</InputLabel>
                                        <Select
                                            labelId="demo-simple-select-outlined-label"
                                            id="demo-simple-select-outlined"
                                            label="Report Status"
                                            name="ComplaintStatus"
                                            value={this.state.EMSComplaint.ComplaintStatus}
                                            onChange={this.handleChange}>
                                            <MenuItem value=""><em>All</em></MenuItem>
                                            <MenuItem value="Submitted">Submitted</MenuItem>
                                            <MenuItem value="Under Review">Under Review</MenuItem>
                                            <MenuItem value="Reviewed">Reviewed</MenuItem>
                                            <MenuItem value="Denied">Denied</MenuItem>
                                            <MenuItem value="Completed">Completed</MenuItem>
                                        </Select>
                                    </FormControl>
                                </Grid> : <Grid item xs={4} sm={4}></Grid>}
                            <Grid item xs={4} sm={4}>
                                <FormControl variant="outlined" className="selectForm" style={{ width: '100%' }}>
                                    <InputLabel id="demo-simple-select-outlined-label" style={{ fontSize: 13 }}>*Location</InputLabel>
                                    <Select
                                        labelId="demo-simple-select-outlined-label"
                                        id="demo-simple-select-outlined"
                                        name="Location"
                                        label="Location"
                                        value={this.state.EMSComplaint.Location}
                                        onChange={this.handleChange}>
                                        <MenuItem value=""><em>Select One</em></MenuItem>
                                        {this.state.WorkGroupList.map((item) => (
                                            <MenuItem value={item.Id}><em>{item.WorkGroupName}</em></MenuItem>
                                        ))}
                                    </Select>
                                </FormControl>
                            </Grid>
                            <Grid item xs={4} sm={4}>
                                <FormControl variant="outlined" className="selectForm" style={{ width: '100%' }}>
                                    <InputLabel id="demo-simple-select-outlined-label" style={{ fontSize: 13 }}>*Shift</InputLabel>
                                    <Select
                                        labelId="demo-simple-select-outlined-label"
                                        id="demo-simple-select-outlined"
                                        name="SubmitterShit"
                                        label="Shift"
                                        value={this.state.EMSComplaint.SubmitterShit}
                                        onChange={this.handleChange}>
                                        <MenuItem value="A"><em>A</em></MenuItem>
                                        <MenuItem value="B"><em>B</em></MenuItem>
                                        <MenuItem value="C"><em>C</em></MenuItem>
                                        <MenuItem value="D"><em>D</em></MenuItem>
                                    </Select>
                                </FormControl>
                            </Grid>
                            <Grid item xs={4} sm={4}>
                                <FormControl variant="outlined" className="selectForm" style={{ width: '100%' }}>
                                    <InputLabel id="demo-simple-select-outlined-label" style={{ fontSize: 13 }}>*Unit</InputLabel>
                                    <Select
                                        labelId="demo-simple-select-outlined-label"
                                        id={this.state.EMSComplaint.Unit.length > 0 ? '' : 'requiredFieldLabel'}
                                        name="Unit"
                                        label="Unit"
                                        value={this.state.EMSComplaint.Unit}
                                        onChange={this.handleChange}>
                                        <MenuItem value=""><em>Select One</em></MenuItem>
                                        {this.state.UnitList.map((item) => (
                                            <MenuItem value={item.Id}><em>{item.Unit}</em></MenuItem>
                                        ))}
                                    </Select>
                                </FormControl>
                            </Grid>
                            <Grid item xs={6} sm={6}>
                                <TextField name="SerialNumber" id={this.state.EMSComplaint.SerialNumber.length > 0 ? '' : 'requiredFieldLabel'} className="errorMessage" placeholder="Serial Number" value={this.state.EMSComplaint.SerialNumber} onChange={this.handleChange} helperText="Serial Number" inputProps={{ maxLength: 11 }} />
                                <span style={{ color: 'blue', fontSize: '11px', padding: '1.5px' }}></span>
                            </Grid>
                            <Grid item xs={6} sm={6}>
                                <TextField name="VehicleNumber" className="errorMessage" placeholder="Vehicle Number" value={this.state.EMSComplaint.VehicleNumber} onChange={this.handleChange} helperText="Vehicle Number" inputProps={{ maxLength: 11 }} />
                                <span style={{ color: 'blue', fontSize: '11px', padding: '1.5px' }}></span>
                            </Grid>
                            <Grid item xs={6} sm={6}>
                                <TextField name="AssetNumber" id={this.state.EMSComplaint.AssetNumber.length > 0 ? '' : 'requiredFieldLabel'} className="errorMessage" placeholder="County Asset Number" value={this.state.EMSComplaint.AssetNumber} onChange={this.handleChange} helperText="County Asset Number" inputProps={{ maxLength: 10 }} />
                                <span style={{ color: 'blue', fontSize: '11px', padding: '1.5px' }}></span>
                            </Grid>
                            <Grid item xs={6} sm={6}>
                                <TextField name="IncidentNumber" className="errorMessage" placeholder="Incident Number" value={this.state.EMSComplaint.IncidentNumber} onChange={this.handleChange} helperText="Incident Number" inputProps={{ maxLength: 11 }} />
                                <span style={{ color: 'blue', fontSize: '11px', padding: '1.5px' }}></span>
                            </Grid>
                            <Grid item xs={12} sm={12}>
                                <TextField
                                    name="ComplaintDetail"
                                    id="filled-multiline-static"
                                    label="Complaint Detail"
                                    multiline
                                    rows={5}
                                    className="errorMessage"
                                    placeholder="Complaint Detail"
                                    style={{ width: '100%' }}
                                    value={this.state.EMSComplaint.ComplaintDetail}
                                    onChange={this.handleChange}
                                />
                            </Grid>
                            <Grid item xs={12}>
                            {this.props.PageName && this.props.PageName === 'Admin' ?
                                <Grid item xs={12}>
                                        <TextField
                                            name="CommentsByAdmin"
                                            id="filled-multiline-static"
                                            label="Comment Box"
                                            multiline
                                            rows={5}
                                            className="errorMessage"
                                            placeholder="Write a Comment"
                                            style={{ width: '100%' }}
                                            value={this.state.EMSComplaint.CommentsByAdmin}
                                            onChange={this.handleChange}
                                        />
                                </Grid> : <Grid item xs={12}></Grid>}
                                <EMSSubmitTab ref={this.eMSSubmitTab} ComplaintId={this.state.EMSComplaint.ComplaintId} PageName={this.props.PageName} />
                            </Grid>
                            <Grid item xs={12} className="textRight popupCloseBtn">
                                <Button variant='contained' color="primary" onClick={() => { this.handleSubmit(); }}>Update Complaint</Button>
                                <Button variant='contained' color="default" onClick={() => { this.handleClear(); this.handleClose(); }}>Close</Button>
                            </Grid>
                        </Grid>
                    </Paper>
                </Modal>
            </Grid>
        )
    }

    componentDidMount() {
        this.getEMSCategory();
        this.getWorkGroup();
        this.getUnit();
    }
}